body {
  background-color: #f4f4f9;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.container {
  background: #fff;
  border-radius: 8px;
  max-width: 600px;
  margin: 20px auto;
  padding: 20px;
  box-shadow: 0 2px 4px #0000001a;
}

h1 {
  text-align: center;
}

form {
  flex-direction: column;
  gap: 10px;
  display: flex;
}

input {
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 10px;
  font-size: 16px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  padding: 10px 15px;
  font-size: 16px;
}

button:hover {
  background-color: #0056b3;
}

.movie-list {
  padding: 0;
  list-style: none;
}

.movie-item {
  background: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 4px;
  justify-content: space-between;
  align-items: center;
  margin: 5px 0;
  padding: 10px;
  display: flex;
}

.movie-details {
  word-break: break-word;
  flex: 1;
}

.movie-buttons {
  gap: 10px;
  display: flex;
}
